html,
body {
  height: 100%;
}
body {
  margin: 0;
  background: -webkit-linear-gradient(45deg, #49a09d, #5f2c82);
  background: linear-gradient(45deg, #49a09d, #5f2c82);
  font-family: sans-serif;
  font-weight: 100;
  color: #fff;
}
.container {
  position: absolute;
  top: 50%;
  left: 50%;
  max-height: 600px;
  width: 800px;
  height: 800px;
  overflow-y: scroll;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
table {
  width: 800px;
  height: 800px;
  border-collapse: collapse;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
th,
td {
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.2);
  color: #fff;
}
th {
  text-align: left;
}
thead th {
  background-color: #55608f;
}
tbody tr:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
tbody td {
  position: relative;
}
tbody td:hover:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: -9999px;
  bottom: -9999px;
  background-color: rgba(255, 255, 255, 0.2);
  z-index: -1;
}

.center {
  display: grid;
  place-items: center;
  height: 100vh;
}

a {
  text-decoration: none;
}

.select {
  padding: 10px;
}

.logo {
  display: block;
  margin: 0 auto;
}

.center_nav {
  display: flex;
  justify-content: center;
}

.link {
  margin: 0 10px;
  color: rgb(197, 197, 235);
  text-decoration: none;
  transition: color 0.3s ease;
}

.link:hover {
  color: rgb(170, 214, 241);
}
